<template>
  <div>
    <TypeNav></TypeNav>
    <div class="swiper-container" ref="mySwiper">
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for="(class1,index) in classList" :key="class1.id">
          <img :src="class1.imgUrl" alt="">
        </div>
      </div>
      <!-- 如果需要分页器 -->
      <div class="swiper-pagination"></div>
      <!-- 如果需要导航按钮 -->
      <div class="swiper-button-prev"></div>
      <div class="swiper-button-next"></div>
    </div>

  </div>
</template>

<script>
import TypeNav from '@/components/TypeNav/index.vue'
import Swiper from 'swiper/js/swiper.js'
import { mapState } from 'vuex'
export default {
  components: {
    TypeNav
  },
  mounted () {
    this.$store.dispatch('getClassList')
    setTimeout(() => {
      var mySwiper = new Swiper(this.$refs.mySwiper, {
        // 首尾相接
        loop: true,
        speed: 1500,
        // 自动播放
        autoplay: {
          delay: 2000,
          // 鼠标介入操作停止
          disableOnIneraction: true
        },
        // 如果需要分页器
        pagination: {
          el: '.swiper-pagination',
          // 点击小球的时候也切换图片
          clickable: true
        },
        // 如果需要前进后退按钮
        navigation: {
          nextEl: '.swiper-button-next',
          prevEl: '.swiper-button-prev'
        },
        effect: 'fade',
        fadeEffect: {
          crossFade: true
        }
      })
    }, 200)
  },
  computed: {
    ...mapState({
      classList: state => state.classmate.classList
    })
  }
}
</script>

<style lang='less' scoped>
.swiper-slide{
  text-align: center;
  margin-top:50px ;
  margin-bottom: 50px;
img {
  position: relative;
  height: 500px;
}
}

</style>
