<template>
  <!-- 商品分类导航 -->
  <div class="type-nav">
    <div class="container">
      <div @mouseleave="leavecurrent(index)"  @mouseenter="enterShow">
        <h2 class="all">全部商品分类</h2>
        <!-- 过渡动画 -->
         <el-collapse-transition>
        <div class="sort" v-if="show" >
          <div class="all-sort-list2" @click="goSearch">
            <div class="item" v-for="(item,index) in categoryList" :key="item.categoryId" :class="{cur:currentindex==index}">
              <h3 @mouseenter="current(index)">
                <a :data-categoryName="item.categoryName" :data-category1Id="item.categoryId">{{item.categoryName}}</a>
              </h3>
              <div class="item-list clearfix" :style="{display:currentindex==index?'block':'none'}">
                <div class="subitem" v-for="(item2,index) in item.categoryChild" :key="item2.categoryId">
                  <dl class="fore">
                    <dt>
                      <a :data-categoryName="item2.categoryName" :data-category2Id="item2.categoryId">{{item2.categoryName}}</a>
                    </dt>
                    <dd>
                      <em v-for="(item3,index) in item2.categoryChild" :key="item3.categoryId">
                        <a :data-categoryName="item3.categoryName" :data-category3Id="item3.categoryId">{{item3.categoryName}}</a>
                      </em>

                    </dd>
                  </dl>
                </div>
              </div>
            </div>
          </div>
        </div>
         </el-collapse-transition>
      </div>

      <nav class="nav">
        <a href="###">服装城</a>
        <a href="###">美妆馆</a>
        <a href="###">尚品汇超市</a>
        <a href="###">全球购</a>
        <a href="###">闪购</a>
        <a href="###">团购</a>
        <a href="###">有趣</a>
        <a href="###">秒杀</a>
      </nav>

    </div>
  </div>
</template>

<script>
import { mapState } from 'vuex'
// 引入lodash中得节流函数
import throttle from 'lodash/throttle.js'
import 'element-ui/lib/theme-chalk/base.css'
// collapse 展开折叠
import CollapseTransition from 'element-ui/lib/transitions/collapse-transition'
import Vue from 'vue'

Vue.component(CollapseTransition.name, CollapseTransition)
export default {
  name: 'TypeNav',
  data () {
    return {
      currentindex: -1,
      index: '',
      show: true
    }
  },
  mounted () {
    this.$store.dispatch('categoryList')
    // 只要hash地址不是首页，全部商品分类就是隐藏
    if (this.$route.path !== '/home') {
      this.show = false
    }
  },
  computed: {
    ...mapState({
      // state参数为大仓库中的state数据，所以拿state下home里面的categoryList
      categoryList: state => {
        return state.home.categoryList
      }
    })
  },
  methods: {
    // current (index) {
    //   this.currentindex = index
    // },
    // 添加节流写法
    current: throttle(function (index) {
      this.currentindex = index
    }, 50),
    // 鼠标移除全部商品分类后导航消失
    leavecurrent () {
      this.currentindex = -1
      // 只要hash地址不是首页，全部商品分类就是隐藏
      if (this.$route.path !== '/home') {
        this.show = false
      }
    },
    // goSearch (event) {
    //   // event的形参为这个goSearch函数所有的事件信息
    //   // event里面的target值为标签属性event.target获得当前点击的标签
    //   const node = event.target
    //   const {
    //     categoryname,
    //     category1id,
    //     category2id,
    //     category3id
    //   } = node.dataset
    //   if (categoryname) {
    //     const location = { name: 'search' }
    //     // console.log(location)
    //     // ,打印出来的是categoryname名字一级名字
    //     const qurey = { categoryName: categoryname }
    //     // console.log(qurey)
    //     // 打印出来的是categoryname名字一级名字，和一级名字对应的id
    //     if (category1id) {
    //       qurey.category1Id = category1id
    //     } else if (category2id) {
    //       qurey.category2Id = category2id
    //     } else {
    //       qurey.category3Id = category3id
    //     }
    //     // 将赋值好的名字和id一起给location
    //     location.qurey = qurey
    //     // 路由跳转
    //     this.$router.push(location)
    //   }
    // },
    enterShow () {
      this.show = true
    },
    goSearch (event) {
      // event.target:获取到的是出发事件的元素(div、h3、a、em、dt、dl)
      const node = event.target
      // 给a标签添加自定义属性data-categoryName,全部的字标签当中只有a标签带有自定义属性，别的标签名字----dataset纯属扯淡
      const {
        categoryname,
        category1id,
        category2id,
        category3id
      } = node.dataset
      // 第二个问题解决了：点击的到底是不是a标签（只要这个标签身上带有categoryname）一定是a标签
      // 当前这个if语句：一定是a标签才会进入
      if (categoryname) {
        // 准备路由跳转的参数对象
        const loction = { name: 'search' }
        const query = { categoryName: categoryname }
        // 一定是a标签：一级目录
        if (category1id) {
          query.category1Id = category1id
          // 一定是a标签：二级目录
        } else if (category2id) {
          query.category2Id = category2id
          // 一定是a标签：三级目录
        } else {
          query.category3Id = category3id
        }
        // 判断：如果路由跳转的时候，带有params参数，捎带脚传递过去
        if (this.$route.params) {
          loction.params = this.$route.params
          // 动态给location配置对象添加query属性
          loction.query = query
          // 路由跳转
          this.$router.push(loction)
        }
      }
    }
  }
}
</script>

<style lang="less" scoped>
.type-nav {
  border-bottom: 2px solid #e1251b;

  .container {
    width: 1200px;
    margin: 0 auto;
    display: flex;
    position: relative;

    .all {
      width: 210px;
      height: 45px;
      background-color: #e1251b;
      line-height: 45px;
      text-align: center;
      color: #fff;
      font-size: 14px;
      font-weight: bold;
    }

    .nav {
      a {
        display: block;
        float: left;
        width: 80px;
        height: 45px;
        padding: 0 10px;
        text-align: center;
        line-height: 45px;
        font-size: 16px;
        color: #333;
        text-decoration: none;
      }
      a:hover {
        background-color: #e1251b;
        color: #fff !important;
      }
    }

    .sort {
      position: absolute;
      left: 0;
      top: 45px;
      width: 210px;
      height: 461px;
      position: absolute;
      background: #fafafa;
      z-index: 999;
      // overflow: hidden;

      .all-sort-list2 {
        .item {
          h3 {
            line-height: 30px;
            font-size: 14px;
            font-weight: 400;
            overflow: hidden;
            padding: 0 20px;
            margin: 0;

            a {
              color: #333;
            }
          }

          .item-list {
            display: none;
            position: absolute;
            width: 734px;
            min-height: 460px;
            background: #f7f7f7;
            left: 210px;
            border: 1px solid #ddd;
            top: 0;
            z-index: 9999 !important;

            .subitem {
              float: left;
              width: 650px;
              padding: 0 4px 0 8px;

              dl {
                border-top: 1px solid #eee;
                padding: 6px 0;
                overflow: hidden;
                zoom: 1;

                &.fore {
                  border-top: 0;
                }

                dt {
                  float: left;
                  width: 54px;
                  line-height: 22px;
                  text-align: right;
                  padding: 3px 6px 0 0;
                  font-weight: 700;
                }

                dd {
                  float: left;
                  width: 415px;
                  padding: 3px 0 0;
                  overflow: hidden;

                  em {
                    float: left;
                    height: 14px;
                    line-height: 14px;
                    padding: 0 8px;
                    margin-top: 5px;
                    border-left: 1px solid #ccc;
                  }
                }
              }
            }
          }
        }
      }
    }
  }
}
.cur {
  margin-left: 3px;
  background-color: rgba(#e1251b, 0.6);
  h3 {
    a {
      color: #fff !important;
      text-decoration: none;
    }
  }
}
</style>
