// 导入API
import { reqClassList } from '@/api/index.js'
const state = {
  classList: []
}
const mutations = {
  classlist (state, classList) {
    state.classList = classList
  }
}
const actions = {
  async getClassList ({ commit }) {
    const res = await reqClassList()
    if (res.code === 200) {
      commit('classlist', res.data)
      console.log(res)
    }
  }
}
const getters = {

}
export default {
  state,
  mutations,
  actions,
  getters
}
