// 导入API
import { reqCategoryList, reqBannerList, reqFloorList } from '@/api/index.js'

// 仓库存数据的地方
const state = {
  categoryList: [],
  bannerList: [],
  floorList: []
}
// 修改state里面数据的唯一手段
const mutations = {
  catelist (state, categoryList) {
    state.categoryList = categoryList
  },
  bannerlist (state, bannerList) {
    state.bannerList = bannerList
  },
  floorlist (state, floorList) {
    state.floorList = floorList
  }
}
// 可以书写自己的业务逻辑
const actions = {
  // 调用API里面的接口函数，向服务器发送请求
  async categoryList ({ commit }) {
    const res = await reqCategoryList()
    if (res.code === 200) {
      commit('catelist', res.data)
    }
  },
  // 获取首页轮播图mock的数据
  async getBannerList ({ commit }) {
    const res = await reqBannerList()
    // console.log('这是轮播图' + res)
    if (res.code === 200) {
      // 如果res.code延迟是200的话就是成功的意思，然后提交到自定义名称bannerlist,提交res里面的data
      commit('bannerlist', res.data)
    }
  },
  // 获取首页楼层的数据
  async getFloorList ({ commit }) {
    const res = await reqFloorList()
    console.log(res)
    if (res.code === 200) {
      commit('floorlist', res.data)
    }
  }
}
// 计算属性，用于简化仓库数据，让组件获取数据更方便
const getters = {}
export default {
  state,
  mutations,
  actions,
  getters
}
