                    <div class="loginList">
                        <p>ZK欢迎您！</p>
                        <p>
                            <span>请</span>
                            <a href="javascript:;" @click="login">登录</a>
                            <router-link to="/register" class="register" >免费注册</router-link>
                        </p>
                    </div>
                    <div class="typeList">
                        <a href="###">我的订单</a>
                        <a href="###">我的购物车</a>
                        <a href="###">我的尚品汇</a>
                        <a href="###">尚品汇会员</a>
                        <a href="###">企业采购</a>
                        <a href="###">关注尚品汇</a>
                        <a href="###">合作招商</a>
                        <a href="###">商家后台</a>
                    </div>
                </div>
            </div>
            <!--头部第二行 搜索区域-->
            <div class="bottom">
                <h1 class="logoArea">
                    <router-link class="logo" title="尚品汇" to="/home">
                        <img src="./images/logo.png" alt="">
                    </router-link>
                </h1>
                <div class="searchArea">
                    <form action="###" class="searchForm">
                        <input type="text" id="autocomplete" class="input-error input-xxlarge"  v-model="keyword" placeholder="  搜索你喜欢的物品试试看吧"/>
                        <button class="sui-btn btn-xlarge btn-danger" type="button" v-on="{click:goSearch,keydown:goSearch}">搜索</button>
                    </form>
                </div>
                 <Datetime class="time"></Datetime>
            </div>
        </header>
</template>

<script>
import Datetime from './datetime.vue'
export default {
  components: {
    Datetime
  },
  data () {
    return {
      keyword: ''
    }
  },
  methods: {
    login () {
      this.$router.push('/login')
    },
    goSearch () {
      // 代表的是如果有query参数也带过去
      if (this.$route.query) {
        const loction = {
          name: 'search',
          params: { keyword: this.keyword || undefined }
        }
        loction.query = this.$route.query
        this.$router.push(loction)
      }
      if (this.keyword === '张朔') {
        this.$router.push('/classmate')
      }
    }
  }
}
</script>

